import React from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";

// Depending on the current path, this component sets the "active" class on the appropriate navigation link item
const Navbar = props => (
  <nav className="navbar navbar-default">
    <div className="container-fluid">
      <div className="navbar-header">
        <Link className="navbar-brand" to="/about">
          Kidly
        </Link>
        <img src="images/kinder-logo.PNG" style={{ borderRadius: 50, width: 20, marginLeft: -10 }} />
      </div>
      <ul className="nav navbar-nav">
        <li
          className={
           window.location.pathname === "/about"
              ? "active"
              : ""
          }
        >
          <Link to="/about">About</Link>
        </li>
        <li className={window.location.pathname === "/discover" ? "active" : ""}>
          <Link to="/discover">Discover</Link>
        </li>
        <li className={window.location.pathname === "/signup" ? "active" : ""}>
          <Link to="/signup">Provider SignUp</Link>
        </li>
        <li className={window.location.pathname === "/" ? "active" : ""}>
          <Link to="/">Log Out</Link>
        </li>                     
      </ul>
    </div>
  </nav>
);

export default Navbar;



